<template>
    <ul id="ul_applist">
        <li v-bind:key="item.id" v-for="item in items">{{ item.name }}</li>
    </ul>
</template>

<style>
#ul_applist {
    width: 100px;
    text-align: left;
}
</style>


<script>
export default {
  name: "appList",
  data() {
    return {
      items: []
    };
  },
  mounted: function() {
    fetch("./inventory/app/list")
      .then(res => {
        return res.json();
      })
      .then(list => {
        if (list && list.length > 0) {
          for (let i = 0; i < list.length; i++) {
            let app = list[i];
            this.items.push({ id: app.id, name: app.name });
          }
        }
      })
      .catch(ex => {
        console.log(ex);
      });
  }
};
</script>


